<template>
    <div class="inputBox shadow">
      <input type="text" v-model="newTodoItem" v-on:keyup.enter="AddTodo">
      <span class="addContainer" v-on:click="AddTodo">
        <i class="far fa-plus-square addBtn"></i>
      </span>
    </div>
 </template>
 
 <script>
 export default {
   name: 'MainView',
   data:function(){
    return{
      match:"할일항목을 입력해주세요",
      newTodoItem: ""
    }
   },
   methods: {
    AddTodo: function() {
      console.log(this.newTodoItem);  // this는 현재 컴포넌트(MainView.vue)
      //저장하는 로직                  // newTodoItem은 속성(그릇)
      localStorage.setItem(this.newTodoItem, this.newTodoItem);
      this.newTodoItem="..."; //비워주기(초기화) =대입 연산자(널어라)

    },
    clearInput: function() {
      this.newTodoItem=""; //비워주기(초기화)
    }
   }
 }
 </script>

